
  <div>
    <div class="section">
      <h1>Creator Sites Components</h1>

      <pre><code>import { SSProSimulator, UrlBar } from 'streamlabs-beaker';

components: {
  SSProSimulator,
  UrlBar
}</code></pre>
    </div>

    <div class="section">
      <h2>Creator Sites Simulator</h2>

      <DemoSection title="Creator Sites Simulator" :code="demoCode">
        <template #components>
          <SSProSimulator :username="username" :domain="domain" />
        </template>
      </DemoSection>
    </div>

    <div class="section">
      <table class="docs-table">
        <thead>
          <tr>
            <th>Prop</th>
            <th>Type</th>
            <th>Default</th>
            <th>Description</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>domain</td>
            <td>string</td>
            <td>-</td>
            <td>Domain name for the Url Bar.</td>
          </tr>
          <tr>
            <td>icon</td>
            <td>string</td>
            <td>
              "https://live.kickstarter.com/images/avatar/medium/avatars4.png"
            </td>
            <td>Set this to users platform icon.</td>
          </tr>
          <tr>
            <td>username</td>
            <td>string</td>
            <td>"Awkward_Raccoon"</td>
            <td>Set this to users platform username.</td>
          </tr>
        </tbody>
      </table>
    </div>

    <div class="section">
      <h2>URL Bar</h2>

      <DemoSection title="URL Bar" :code="demoCode">
        <template #components>
          <UrlBar :domain="domain" />
        </template>
      </DemoSection>
    </div>

    <div class="section">
      <table class="docs-table">
        <thead>
          <tr>
            <th>Prop</th>
            <th>Type</th>
            <th>Default</th>
            <th>Description</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>domain</td>
            <td>string</td>
            <td>-</td>
            <td>Domain name for the Url Bar.</td>
          </tr>
        </tbody>
      </table>
    </div>

    <div class="section">
      <h2>Creator Sites Layout Picker</h2>

      <DemoSection title="Layout Picker" :code="demoCode">
        <template #components>
          <CSLayoutPicker>
            <div
              slot="layouts"
              v-for="layout in webLayouts"
              :key="layout.id"
              class="s-cs-layout-picker__layout"
            >
              <div>
                <img :src="layout.src" />
              </div>
            </div>
          </CSLayoutPicker>
        </template>
      </DemoSection>
    </div>
  </div>
